},
closeBg(){
this.darkbgShow = false;
},
score(){
axios.get('pscoreparty/getSumScoreByUserId',{
params:{
userId:this.$store.getters.user.userid,
year:new Date().getFullYear()
}
})
.then( res => {
this.itegral = res.data;
}).catch(err => {
console.log('fail'+err);
})

},
dataPickers(dates){
let times = new Date(dates).toLocaleString();
Date.prototype.toLocaleString = function() {
return this.getFullYear() + "" + (this.getMonth()+1) + "" + this.getDate() + " "
};
return times;
},
reSubmit(item){
this.setInfo(item);
this.$router.push(`points/addPoint1/${item.projectid}/${item.moduleid}/${item.studyid}`);
},
changeTable()
{
this.tabIndex = 1;
},
...mapActions(['setInfo'])
},
mounted(){
this.progress();
this.project();
this.getDetail();
this.rating();
this.score();
this.picAccept = this.$route.params.pictureSex;
}
